* {
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

html,
body {
  overflow-x: hidden;
  font-family: "Cabin", sans-serif;
  max-height: 100%;
  background-image: linear-gradient(
    45deg,
    hsl(33deg 85% 65%) 0%,
    hsl(32deg 84% 63%) 11%,
    hsl(30deg 82% 60%) 22%,
    hsl(28deg 81% 57%) 33%,
    hsl(26deg 80% 55%) 44%,
    hsl(24deg 79% 52%) 56%,
    hsl(22deg 77% 50%) 67%,
    hsl(19deg 83% 48%) 78%,
    rgb(219, 70, 15) 89%,
    rgb(218, 49, 11) 100%
  );
}

header {
  display: flex;
  justify-content: center;
  padding: 10px;
}

nav {
  position: fixed;
  text-align: center;
  padding: 25px 0px;
  z-index: 1000;
}

ul {
  display: flex;
  gap: 50px;
  list-style: none;
}

li {
  color: white;
  text-decoration: none;
}

.active{
  background-color: #FFAF22;
  color: black;
  border-radius: 20px;
  padding: 10px 25px;
}

a {
  text-decoration: none;
  color: white;
}

nav ul li .underline .active{
  width: 100%;
  height: 3px;
  background-color: red;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}
.back-button {
  position: fixed;
}
.container-about {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.container-about {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.sectionhome {
  position: absolute;
  top: 95%;
  margin: auto;
  color: white;
  font-size: 1vmin;
  width: 33.33%;
  text-align: center;
}

.arrow {
  width: 50px;
}

.contact-info-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(
    45deg,
    hsl(11deg 90% 45%) 100% hsl(16deg 87% 46%) 89%,
    hsl(19deg 83% 48%) 78%,
    hsl(22deg 77% 50%) 67%,
    hsl(24deg 79% 52%) 56%,
    hsl(26deg 80% 55%) 44%,
    hsl(28deg 81% 57%) 33%,
    hsl(32deg 84% 63%) 11%,
    hsl(32deg 84% 63%) 11%,
    hsl(33deg 85% 65%) 0%
  );
}

.contact-info {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.title:hover {
  cursor: url(""), auto;
}
.clicked-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform: rotate(-10deg);
}
.clicked {
  width: 30px;
  animation: growUp 1s ease-in infinite;
}

#name {
  text-transform: uppercase;
  font-size: 30px;
  color: white;
  font-weight: 400;
}

.title {
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  margin: 15px 0px;
}

.info {
  display: block;
  font-size: 30px;
  text-transform: uppercase;
  color: white;
  text-decoration: none;
}

@keyframes growUp {
  0% {
    transform: scale(90%);
  }
  50% {
    transform: scale(100%);
  }
  100% {
    transform: scale(90%);
  }
}

@media (max-width: 500px) and (min-width: 200px) {
  .projects-div {
    margin: auto;
  }

  .aboutme-div {
    margin: auto;
  }

  .contact-div {
    margin: auto;
  }
  .sectionhome {
    position: relative;
  }

  .sectionhome {
    display: none;
  }

  .sub-title {
    font-size: 0.78em;
  }
}
